.wrapper {
  @media (max-width: 599px) {
    overflow: hidden;
  }
}

.header {
  position: fixed;
  z-index: 10;
  width: 100%;
  top: 0;
  left: 0;

  &-top {
    background-color: $primary;

    &-logo {
      width: 220px;
      height: auto;
      margin-top: auto;
      margin-bottom: auto;
      margin-right: 12px;

      @media (max-width: 1680px) {
        width: 200px;
      }
      @media (max-width: 1366px) {
        width: 180px;
      }
      @media (max-width: 1280px) {
        width: 170px;
      }
    }

    &-burger {
      position: relative;
      width: 22px;
      height: 22px;     
      padding: 0;
      border: none;
      background-color: transparent;
      cursor: pointer;
      display: none;
      margin-right: 12px;

      @media (max-width: 960px) {
        display: block;
      }

      span {
        height: 2px;
        width: 100%;
        border-radius: 2px;
        background-color: #fff;
        display: block;
        position: relative;
        &:before,
        &:after {
          content: "";
          display: block;
          width: 100%;
          height: 2px;
          border-radius: 2px;
          background-color: #fff;
          position: absolute;
          left: 0;
          transition: transform 0.3s ease-in-out;
        }   
        &:before {
          top: -8px;         
        }
        &:after {         
          top: 8px;
        }    
        &.active {
          transform: rotate(45deg);
          &:before {
            top: 0;
            transform: rotate(-90deg);
          }
          &:after {
            top: 0;
            transform: rotate(90deg);
          }
        }
      }
    }

    &-wrapper {
      display: flex;
      align-items: center;
      height: 80px;

      @media (max-width: 1680px) {
        height: 70px;
      }
      @media (max-width: 1366px) {
        height: 60px;
      }
      @media (max-width: 960px) {
        justify-content: space-between;
        height: 74px;
      }
    }

    &-contact {
      display: flex;
      align-items: center;
      // gap: 40px;
      margin-left: auto;
      margin-right: 40px;
      @include fz(18);
      @include ls(0.72);

      @media (max-width: 1680px) {
        @include fz(16);
      }
      @media (max-width: 1366px) {
        @include fz(14);
      }
      @media (max-width: 1280px) {
        @include fz(12);
      }
      @media (max-width: 1140px) {
        @include fz(11);
      }
      @media (max-width: 960px) {
        display: none;
      }
    }

    &-sign {
      &__desktop {
        @media (max-width: 960px) {
          display: none;
        }
      }
      &__mobile {
        display: none;
        @media (max-width: 960px) {
          display: block;
        }
      }
    }

    &-icon {
      width: 32px;
      height: 32px;
      @media (max-width: 1680px) {
        width: 30px;
        height: 30px;
      }
      @media (max-width: 1366px) {
        width: 24px;
        height: 24px;
      }
    }
    &-address {
      display: flex;
      align-items: center;
      gap: 12px;
      color: #fff;
      margin-right: 46px;
      @media (max-width: 1680px) {
        gap: 10px;
        margin-right: 36px;
      }
      @media (max-width: 1366px) {
        gap: 8px;
        margin-right: 30px;
      }
    }

    &-phone {
      display: flex;
      align-items: center;
      gap: 20px;
      margin-right: 20px;
      @media (max-width: 1680px) {
        gap: 16px;
        margin-right: 16px;
      }
      @media (max-width: 1366px) {
        gap: 14px;
        margin-right: 14px;
      }
      a {
        display: inherit;
        color: #fff;        
      }
    }

    &-socials {
      display: flex;
      align-items: center;
      a {       
        display: inherit;          
      }
    }
  }
  &-nav {
    &-search {
      margin-left: auto;
      cursor: pointer;
      position: relative;
      .search-icon {
        width: 26px;
        height: 26px;
        @media (max-width: 1680px) {
          width: 24px;
          height: 24px;
        }
        @media (max-width: 1366px) {
          width: 22px;
          height: 22px;
        }
        @media (max-width: 1280px) {
          width: 20px;
          height: 20px;
        }
        @media (max-width: 1140px) {
          width: 18px;
          height: 18px;
        }
      }
    }  
  }
  &-bottom {
    background-color: $bgc-pink;

    @media (max-width: 960px) {
      display: none;
    }
    
    &-nav {
      display: flex;
      align-items: center;
      gap: 60px;
      color: #fff;
      height: 60px;
      @media (max-width: 1680px) {
        gap: 50px;
        height: 50px;
      }
      @media (max-width: 1366px) {
        gap: 40px;
        height: 40px;
      }
      @media (max-width: 1280px) {
        gap: 30px;
      }
 
      &-search {
        margin-left: auto;
        cursor: pointer;
        position: relative;
        .search-icon {
          width: 26px;
          height: 26px;
        }
      }  
      .search-wrapper {       
        overflow: hidden;
        visibility: hidden;
        transform: translateY(-10px);
        display: flex;
        align-items: center;
        gap: 10px;
        position: absolute;
        right: 0;
        padding: 12px 20px;
        background-color: #FFF;
        border-radius: 12px;
        box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
        transition: visibility 0.5s, translate 0.5s;
        &.open {         
          visibility: visible;
          transform: translateY(0);
        }
      }
      .ui-field {
        width: max-content;
        position: relative;
      }      
    }

    &-list {
      display: none;
      position: absolute;
      top: -20px;
      left: 100%;
      padding-right: 10px;
      padding-top: 20px;
      padding-bottom: 20px;
      background-color: #fff;
      border-radius: 12px;     
      box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
    }

    nav > ul > li:nth-child(2) > ul {
      padding-right: 0;
      li {
        a {
          border-top-right-radius: 0px !important;
          border-bottom-right-radius: 0px !important;
        }
      }
    }
    nav {   
      & > ul:first-child {
        display: flex;
        gap: 60px;
        align-items: center;
        @media (max-width: 1680px) {
          gap: 46px;
        }
        @media (max-width: 1366px) {
          gap: 40px;
        }
        @media (max-width: 1280px) {
          gap: 30px;
        }
        & > li {
          position: relative;
          &.active {
            &::after {            
              height: 8px;
              width: 100%;
              visibility: visible;
              opacity: 1;
              @media (max-width: 1680px) {
                height: 6px;
                border-radius: 3px 3px 0px 0px;
              }
              @media (max-width: 1140px) {
                height: 4px;
                border-radius: 2px 2px 0px 0px;
              }   
            }
          }  
         
          &::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            height: 0;
            width: 0;
            visibility: hidden;
            opacity: 0;         
            background-color: #CDF3F9;
            border-radius: 4px 4px 0px 0px;       
            transition: width 0.5s ease-in-out;   
            z-index: 1;            
          }
          &:hover {
            &::after {
              height: 8px;
              width: 100%;
              visibility: visible;
              opacity: 1;
            }
          }      
        }
        & > li > a {
          display: inline-block;
          height: 60px;
          display: flex;
          align-items: center;
          @media (max-width: 1680px) {           
            height: 50px;
          }
          @media (max-width: 1366px) {           
            height: 40px;
          }
        }
        li {       
          &:hover {         
            a {
              svg {
                transform: rotate(180deg);
              }
            }
          }
          a {                        
            color: #FFF;
            @include fz(18);     
            font-weight: 500;
            @include ls(0.72);          
            text-transform: uppercase;
            @media (max-width: 1680px) {
              @include fz(16);
            }
            @media (max-width: 1366px) {
              @include fz(14);
            }
            @media (max-width: 1366px) {
              @include fz(12);
            }
            @media (max-width: 1140px) {
              @include fz(10);
            }
  
            svg {
              width: 16px;
              height: 16px;
              flex-shrink: 0;
              margin-left: 6px;
              transition: transform 0.5s;

              @media (max-width: 1680px) {
                width: 14px;
                height: 14px;
              }
              @media (max-width: 1366px) {
                width: 12px;
                height: 12px;
              }
              @media (max-width: 1140px) {
                width: 10px;
                height: 10px;
              }
            }
          }
  
          ul {
            background-color: #fff;
            padding-top: 20px;
            padding-bottom: 20px;
            border-radius: 12px;
            box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
                
            li {
              position: relative;
              height: max-content;           
              
              a {
                display: inline-block;
                color: $font;
                @include fz(18);             
                font-weight: 500;
                @include ls(0.72);              
                text-transform: uppercase;
                color: #222340;
                padding: 12px 20px;           
                max-width: 330px;
                display: flex;
                justify-content: space-between;
                
                svg {
                  width: 20px;
                  height: 20px;
                  transition: opacity 0.5s;
                  margin-left: 16px;
                }
              }

              &:hover .header-bottom-list {
                display: block;                             
                ul {
                  display: block;                     
                }
              }
  
              // &:hover > ul {
              //   display: block;           
              // }
  
              &:hover {
                a {
                  svg {
                    opacity: 0;
                    transform: none;
                  }
                }
              }
  
              ul {
                display: none; 

                // position: absolute;
                // top: -20px;
                // left: 100%;              
                // min-width: 350px;               
                // padding-top: 20px;
                // padding-bottom: 20px;
                padding-right: 10px;
                padding-top: 0;
                padding-bottom: 0;               
                max-height: 420px;
                overflow-y: auto;
                border-radius: 0;
                box-shadow: none;             
               
                li {                               
                  a {                 
                    width: 100%;                   
                    text-transform: unset;   
                    padding-left: 40px;   
                    transition: background-color 0.5s, color 0.5s;
                  }
                  &:hover {
                    a {
                      background-color: $primary;                   
                      border-top-right-radius: 12px !important;
                      border-bottom-right-radius: 12px !important;
                      border-top-left-radius: 0px;
                      border-bottom-left-radius: 0px;
                      color: #fff;
                    }
                  }
                }
              }
            }
          }
        }
      }
  
      & > ul > li > ul {
        padding-left: 20px;
        padding-right: 20px;      
  
        li {        
          a {
            width: 100%;
            transition: background-color 0.5s, color 0.5s;
          }
          &:hover {
            & > a {
              background-color: $primary;
              border-radius: 12px;
              color: #fff;   
            }
          }
        }
      }
  
      li > ul {
        display: none;
        opacity: 0;
        visibility: hidden;
        position: absolute; 
        transition: width 0.5s, height 0.5s, opacity 0.5s;
        top: 50px;
        z-index: 2;
        height: 0;

        @media (max-width: 1680px) {
          top: 40px;
        }
        @media (max-width: 1366px) {
          top: 30px;
        }
        @media (max-width: 1140px) {
          top: 32px;
        }
      }
      li {
        &:hover {       
          ul {
            display: block;         
            visibility: visible;
            opacity: 1;
            width: max-content;
            height: max-content;
          }
        }
      }
    }
  }
}

.main {
  flex-grow: 1;
  padding-top: 140px;
  @media (max-width: 1680px) {           
    padding-top: 110px;
  }
  @media (max-width: 1366px) {
    padding-top: 100px;
  }
  @media (max-width: 960px) {
    padding-top: 60px;
  }
  @media (max-width: 599px) {
    padding-top: 74px;
  }

  &-slider {
    position: relative;     

    &-arrows {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 30px;
      position: absolute;
      bottom: 32px;
      z-index: 2;
      left: 0;
      width: 100%;
      .slider-prev, .slider-next {
        cursor: pointer;
      }
    }
  }

  &-slide {
    height: 820px;     
    padding-top: 232px;
    background-color: #768bce;

    &-bg {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 0;      
      width: 100%;
      height: 100% !important;
      object-fit: cover;
      object-position: center;
    }

    &-info {
      color: #fff;
    }

    &-title {
      color: var(--white, #FFF);
      @include fz(45);      
      font-weight: 600;
      line-height: 141.556%;
      @include ls(1.35);
      text-transform: uppercase;
      margin-bottom: 18px;
    }

    &-descroption {
      @include fz(20);
      font-weight: 400;
      line-height: 155%;
      @include ls(0.2);
      margin-bottom: 30px;
      p {
       margin: 0;
       &:not(:last-of-type) {
        margin-bottom: 12px;
       }
      }     
    }
  }  
}

