// services page
.section-services {    
  padding-bottom: 48px;  
}

.service-cards {    
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.service-card {      
  padding: 20px 40px 40px 40px;
  border-radius: 12px;
  background: #FFF;
  box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
  margin-bottom: 32px;  

  &:last-child {
    padding-bottom: 20px;
  }

  &-head {
    padding: 20px;
    display: flex;
    align-items: center;
    gap: 20px;      
    // transition: color 0.3s;     
    &.border {
      border-bottom: 2px solid $primary;
    }

    &:hover {
      svg {
        path {
          fill: $primary;
          stroke: $primary;          
        }
      }
      .service-card-title {
        color: $primary
      }
    }

    svg {
      path {
        transition: fill .3s, stroke .3s;  
      }

      width: 68px;
      height: 68px;
      flex-shrink: 0;    
    }    
  }
  
  &-title {
    @include fz(24);
    color: $font;
    font-weight: 600;
    line-height: 133.333%;
    transition: color 0.3s;    
  }
  
  &-list {
    ul {
      li {          
        &:not(:last-child) {
          border-bottom: 2px solid $bgc;
        }
        a {
          width: 100%;
          color: $font;
          @include fz(20);             
          font-weight: 400;
          line-height: 155%;          
          @include ls(0.2);
          padding: 11px 20px;
          display: inline-block;         
          border-radius: 12px;

          &:hover {
            background-color: $primary;
            color: #fff;
            transition: background-color 0.3s, color 0.3s;
          }
        }
      }
    }
  }
}

