.benefits {
  padding-top: 32px; 

  &-item {
    height: calc(100% - 32px);
    display: flex;
    align-items: center;
    gap: 36px;
    background-color: #FFF;
    border-radius: 12px;
    box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
    padding-left: 36px;
