      position: relative;
      width: 40px;
      height: 40px;
      border-radius: 50%;     
      background: var(--gradient-gradient-green, linear-gradient(180deg, #319DAC 0%, #278FAF 100%));      

      &.open {
        background: $bgc;        
        svg {
          path {
            transition: stroke 0.5s, opacity 0.5s;
            stroke: $primary;
            &:first-child {
              opacity: 0;
            }
          }
        }
      }

      svg {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &-content {
    max-height: 0; 
    overflow: hidden;
    transition: max-height 0.5s;

