.slider-four {  
  margin: -32px -32px 0;
  padding: 32px 32px 0;
  position: relative;

  @media (max-width: 1024px) {
    margin: -18px -18px 0;
    padding: 18px 18px 0;
  }
  
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: -2px;
    width: 32px;
    height: 100%;  
    z-index: 2;
    @media (max-width: 768px) {
      display: none;
    }
  }
  &::after {
    position: absolute;
    content: '';
    top: 0;
    right: -2px;
    width: 32px;
    height: 100%;   
    z-index: 2;
    @media (max-width: 768px) {
      display: none;
    }
  }
  &.blue {
    &::before {
      background: linear-gradient(to left, rgba(255,255,255,0) 0%, rgba(205, 243, 249,1));   
    }
    &::after {
      background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(205, 243, 249,1));   
    }
  }
  &.blue-light {    
    &::before {
      background: linear-gradient(to left, rgba(255,255,255,0) 0%, rgba(240, 251, 253,1));   
    }
    &::after {
      background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(240, 251, 253,1));
    }
  }  
}

